import paper from 'paper'
export default class Coordinate {
  constructor(config) {
    this.Paper = paper
    this.width = config.width || 500
    this.height = config.height || 500
    this.padding = config.padding || 40
    this.type = config.type || 'log' //  log/normal/polar
    this.data = config.data
    this.xUnit = config.xUnit ? `(${config.xUnit})` : ``
    this.yUnit = config.yUnit ? `(${config.yUnit})` : ``
    this.xLineStyle = {
      strokeWidth: 1,
      strokeColor: '#ccc'
    }
    this.yLineStyle = {
      strokeWidth: 1,
      strokeColor: '#ccc'
    }
    this.rec = null
    this.border = config.border || { color: 'gray', width: 0.5 }
    this.xMax = 1
    this.yMax = 1
    this.xPortion = 10
    this.yPortion = 10
  }
  init() {
    this.rec = new this.Paper.Path.Rectangle(
      new this.Paper.Point(this.padding, this.padding),
      new this.Paper.Point(this.width - this.padding, this.height - this.padding)
    )
    new this.Paper.PointText({
      point: new this.Paper.Point(this.padding / 2, this.padding / 2),
      content: this.yUnit
    })
    new this.Paper.PointText({
      point: new this.Paper.Point(this.width - this.padding / 2, this.height - this.padding / 2),
      content: this.xUnit
    })
    let xArray = this.data.reduce((a, b) => a.concat(b)).map(a => a[0])
    let yArray = this.data.reduce((a, b) => a.concat(b)).map(a => a[1])
    this.rec.strokeColor = this.border.color
    this.xMax = this.setYAxisMaxVal(xArray)
    this.yMax = this.setYAxisMaxVal(yArray)
    if (this.type === 'normal') {
      this.drawNormalCoordinate()
    }
  }
  drawNormalCoordinate() {
    let xFrom = new this.Paper.Point(this.padding, this.padding)
    let xTo = new this.Paper.Point(this.padding, this.height - this.padding)
    let xLine = new this.Paper.Path.Line(xFrom, xTo)
    xLine.style = this.yLineStyle
    let xSymbol = new this.Paper.SymbolDefinition(xLine)
    for (let j = 0; j <= this.xPortion; j++) {
      let xinstance = xSymbol.place()
      xinstance.position = [((this.width - this.padding * 2) / this.xPortion) * j + this.padding, this.height / 2]
      // x 轴坐标位置便宜量
      let xAlix = j ? (Math.log((this.xMax / this.xPortion) * j) / Math.log(10)) * 5 : 0
      // x 坐标轴位置
      let px = ((this.width - this.padding * 2) / this.xPortion) * j + this.padding - xAlix
      let py = this.height - this.padding / 2
      new this.Paper.PointText({
        point: new this.Paper.Point(px, py),
        content: (this.xMax / this.xPortion) * j
      })
    }

    let YFrom = new this.Paper.Point(this.padding, this.padding)
    let yTo = new this.Paper.Point(this.width - this.padding, this.padding)
    let yLine = new this.Paper.Path.Line(YFrom, yTo)
    yLine.style = this.yLineStyle
    let ySymbol = new this.Paper.SymbolDefinition(yLine)
    for (let j = 0; j <= this.yPortion; j++) {
      let yinstance = ySymbol.place()
      yinstance.position = [this.width / 2, ((this.height - this.padding * 2) / this.yPortion) * j + this.padding]
      // y 轴坐标位置便宜量
      let yAlix = j ? (Math.log((this.yMax / this.yPortion) * j) / Math.log(10)) * 5 : 0
      // x 坐标轴位置
      let py = this.height - this.padding - ((this.height - this.padding * 2) / this.yPortion) * j + 10
      let px = this.padding / 2 - yAlix
      new this.Paper.PointText({
        point: new this.Paper.Point(px, py),
        content: (this.yMax / this.yPortion) * j
      })
    }
  }
  transFormPoint([x, y]) {
    if (this.type === 'normal') {
      let pointX = ((this.width - this.padding * 2) * x) / this.xMax + this.padding
      let pointY = this.height - this.padding - ((this.height - this.padding * 2) * y) / this.yMax
      return new this.Paper.Point(pointX, pointY)
    }
  }
  recallNormalPoint(point) {
    let pointX = ((point.x - this.padding) * this.xMax) / (this.width - 2 * this.padding)
    let pointY = ((this.height - point.y - this.padding) * this.yMax) / (this.height - 2 * this.padding)
    return [pointX.toFixed(1), pointY.toFixed(1)]
  }
  setYAxisMaxVal(arrayF) {
    const flatten = arr => {
      while (arr.some(item => Array.isArray(item))) {
        arr = [].concat(...arr)
      }
      return arr
    }
    const returnMaxValue = (array, referMax) => {
      try {
        if (Math.max(...array.flat(Infinity)) > referMax) {
          referMax = referMax - (Math.max(...array.flat(Infinity)) % referMax) + Math.max(...array.flat(Infinity))
        }
        return referMax
      } catch (error) {
        if (Math.max(...flatten(array)) > referMax) {
          referMax = referMax - (Math.max(...flatten(array)) % referMax) + Math.max(...flatten(array))
        }
        return referMax
      }
    }

    let p = (Math.log(Math.max(...arrayF)) / Math.log(10)).toFixed(0)

    let maxValue = Math.pow(10, p - 1)
    maxValue = returnMaxValue(arrayF, maxValue)
    return maxValue
  }
}
