<template>
  <div :style="divStyle">
    <canvas
      id="myCanvas"
      :height="uiConfig.height"
      :width="uiConfig.width"
      style="margin: 10px auto;border: 1px #eee dotted"
    ></canvas>
  </div>
</template>

<script>
import paper from 'paper'
import Coordinate from './../model/Coordinate'

export default {
  name: 'my-paper-graphic',
  data() {
    return {
      activeLine: null,
      activeLineInfo: null,
      activePointInfo: null,
      intersections: null,
      coordinate: null,
      intersectionGroup: null,
      lineArr: []
    }
  },
  props: {
    uiConfig: {
      type: Object
    },
    data: {
      type: Array
    },
    type: {
      type: String
    }
  },
  watch: {
    data: {
      deep: true,
      handler: 'datChange'
    }
  },
  computed: {
    config() {
      return this.uiConfig
    },
    divStyle() {
      return {
        height: this.config.height + 'px',
        width: this.config.width + 'px',
        margin: '20px auto'
      }
    }
  },
  mounted() {
    this.datChange()
  },
  methods: {
    datChange() {
      let $canvas = document.getElementById('myCanvas')
      paper.setup($canvas)

      this.initClass()
      this.lineArr = []


      this.data.forEach(item => {
        this.lineArr.push(
          this.drawLine(
            item.map(a => [a[0], a[1]]),
            this.colorRandom()
          )
        )
      })
      this.mouseEvent()
    },
    initClass() {
      let config = {
        Paper: paper
      }
      Object.assign(config, this.config)
      config.data = this.data
      config.type = this.type
      this.coordinate = new Coordinate(config)
      this.coordinate.init()
    },
    getPoint (event){
      this.lineArr.forEach(path => {
        this.intersections = this.activeLine.getIntersections(path)
        let intersections = this.intersections
        for (var i = 0; i < intersections.length; i++) {
          console.log(intersections[i].point)
          new paper.Path.Circle({
            center: intersections[i].point,
            radius: 3,
            fillColor: 'red',
            parent: this.intersectionGroup
          })
            .removeOnDown()
            .removeOnUp()
          new paper.PointText({
            point: intersections[i].point,
            fillColor: 'red',
            parent: this.intersectionGroup,
            content: this.coordinate?.recallNormalPoint(intersections[i].point)[1] + this.coordinate.yUnit
          })
            .removeOnDown()
            .removeOnUp()
        }
      })
      if (
        event.point.x > this.coordinate.width - this.coordinate.padding ||
        event.point.x < this.coordinate.padding
      ) {
        this.activeLine.strokeColor = '#ffff'
      } else {
        this.activeLine.strokeColor = 'blue'
      }
    },
    mouseEvent() {
      let tool = new paper.Tool()
      this.intersectionGroup = new paper.Group()

      tool.onMouseDown = event => {
        this.intersectionGroup.removeChildren()
        this.activeLine = new paper.Path.Line(
          [event.point.x, this.coordinate.padding],
          [event.point.x, this.coordinate.height - this.coordinate.padding]
        )
        this.activeLine.strokeColor = 'blue'
        this.activeLine.removeOnDown().removeOnUp()
        this.getPoint(event)
        this.activeLineInfo = new paper.PointText({
          point: new paper.Point(event.point.x, this.coordinate.padding / 2),
          content: +Number(this.coordinate.recallNormalPoint(event.point)[0]).toFixed(1) + this.coordinate.xUnit
        })
          .removeOnDown()
          .removeOnUp()
      }
      tool.onMouseDrag = event => {
        this.intersectionGroup.removeChildren()
        this.getPoint(event)
        this.activeLine.position = [event.point.x, this.activeLine.position.y]
        this.activeLineInfo.position = [event.point.x, this.coordinate.padding / 2]
        this.activeLineInfo.content = this.coordinate.recallNormalPoint(event.point)[0] + this.coordinate.xUnit
      }
      tool.onMouseUp = () => {}
    },
    drawLine(arr, color = 'blue') {
      let path = new paper.Path()
      path.strokeColor = color
      arr.forEach(item => {
        path.add(this.coordinate.transFormPoint(item))
      })
      return path
    },
    colorRandom() {
      var a = parseInt(255 - Math.random() * 255).toString(16)
      var b = parseInt(255 - Math.random() * 255).toString(16)
      var c = parseInt(255 - Math.random() * 255).toString(16)
      return  '#' + a + b + c
    }
  }
}
</script>

<style scoped></style>
