<template>
  <div id="app" style="width: 1000px">
    <my-pg :data="data" :type="type" :ui-config="uiConfig"></my-pg>
    <card title="数据管理">
      <AButton @click="addData()">添加数据</AButton>
      <div v-for="(item, index) of data" :key="index">
        数组{{ index + 1 }}：<span v-for="(k, i) of item" :key="i">{{ k }}</span> &nbsp;&nbsp;&nbsp;&nbsp;
        <a @click.prevent="deleteData(index)">删除</a>&nbsp;&nbsp;&nbsp;&nbsp;
        <a @click.prevent="showAdd(index)">添加顶点</a>
      </div>
    </card>
    <card title="使用方法"> </card>
    <Modal title="请添加顶点" v-model="visible" @ok="addPoint">
      <a-input prefix="X" v-model.number="modalx" style="margin: 20px 0" />
      <a-input prefix="Y" v-model.number="modaly" />
    </Modal>
  </div>
</template>

<script>
import MyPg from './components/my-paper-graphic'
// import MyPg from './components/my-pg'
import { Card, Button, Modal, Input } from 'ant-design-vue'
export default {
  name: 'App',
  components: {
    MyPg,
    Card,
    Modal,
    AInput: Input,
    AButton: Button
  },
  data() {
    return {
      timer: 1,
      modalx: 0,
      modaly: 0,
      visible: false,
      currentIndex: '',
      data: [
        [
          [0, 0],
          [800, 800]
        ],
        [
          [20, 30],
          [40, 50],
          [60, 200],
          [300, 70],
          [500, 400]
        ],
        [
          [22, 45],
          [33, 312],
          [444, 432],
          [555, 631],
          [666, 772]
        ]
      ],
      type: 'normal',
      uiConfig: {
        height: 600,
        width: 800,
        yUnit: 's',
        xUnit: 'A'
      }
    }
  },
  methods: {
    deleteData(index) {
      this.data.splice(index, 1)
    },
    addData() {
      this.data.push([])
    },
    showAdd(index) {
      this.currentIndex = index
      this.visible = true
      this.modalx = this.modaly = 0
    },
    addPoint() {
      if (!this.modalx && !this.modaly) {
        return
      }
      this.data[this.currentIndex].push([this.modalx, this.modaly])
    }
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 20px auto;
}
</style>
